<template>
  <v-container
        fluid
        style="align-items: center"
  >
    <v-row 
          align="center" 
          justify="center"
        >
      <v-col cols="5" class="text-center max-min-width-list">
        <div
          class="d-flex align-center"
        >
          <v-text-field
            v-model="taskVal" 
            :label="nameVal ? 'Task' : 'Error Login'"
            :disabled="!nameVal"
            hide-details="auto"
            v-on:keydown.enter="addToList"
          ></v-text-field>
          <v-btn
            v-on:click="addToList"
          >
          <v-icon>mdi-note-plus</v-icon>
        </v-btn>
        
      </div>
        <v-row
          class="mt-2 ml-1"
          justify="start"
          align="center"
        >
          <v-icon
            size="40"
          >
            mdi-account-circle{{ nameVal ? "" : "-outline"}}
          </v-icon>
          <p
            v-if="nameVal"
            class="ml-1 text-h5 mt-4"
          >
            {{ nameVal }}
            <a
              class="ml-1 caption"
              @click="logout"
            >Log Out</a>
          </p>
          <p
            v-else
            class="ml-1 text-h6 mt-3"
          >
            <router-link to="/">Log In</router-link>
          </p>

        </v-row>

        <v-col 
          class="mt-6"
          v-if="todoList.length > 0"
        >
          <v-card
            v-bind:item="item"
            v-for="(item, index) in reversedList"
            v-bind:key="item.id"
            class="c-flex justify-center align-center mb-10"
            :class="{completed: item.completed}"
            style="width: 100%;"
            :disabled="!nameVal"
          >
            <v-row
              class="ml-2 mb-1"
              justify="start"
              align="center"
            >
              <v-btn
                style=""
                class="shrink mr-4 mt-1"
                v-on:click="changeIsCompleted(index, item)"
                :class="{buttonCompleted: item.completed}"
                >
                <v-icon
                  color="green lighten-2"
                
                >mdi-{{item.completed ? "check-bold" : "check-outline"}}</v-icon>
              </v-btn>
              <span
                class="mt-1"
                :class="{userColor: item.name === nameVal}"
              >{{ item.name }}</span>
            </v-row>
            <v-row
              class="ml-5 mb-2 mr-5 mt-4"
              justify="start"
              align="center"
            >
              <span
                v-if="!item.editing"
                class="text-body-1"
                :class="{lineThrough: item.completed}"
                spellcheck="false"
              >{{ item.task }}</span>
              <v-text-field
                v-else
                autofocus 
                v-model="item.task" 
                v-on:blur="updateEditableContent(item, index)"
                v-on:keydown.enter="updateEditableContent(item, index)"
              ></v-text-field>
            </v-row>
            <v-row
              class="ml-2 mr-2 mt-2 mr-2"
              justify="end"
              align="center"
            >
              <span
                class="mb-2 text-body-2"
              >Created: {{ item.date }}</span>
            </v-row>
            <v-row
              class="ml-2 mr-2 mt-2"
              justify="start"
              align="center"
            >
              <v-btn
                class="mb-2 ml-2 mr-2"
                v-on:click="toggleContentEditable(item, index)"
              >
              <v-icon>mdi-{{ !item.editing? "pen" : "check-underline-circle" }}</v-icon>
              </v-btn>
              <v-btn
                class="mb-1"
                v-on:click="deleteTask(index)"
              >
              <v-icon
                color="red darken-4"
              >mdi-delete</v-icon>
              </v-btn>

            </v-row>
          </v-card>
        </v-col>
        <p 
          v-else
          class="mt-5 text-h5"
          >No tasks here</p>
      </v-col>
    </v-row>
  </v-container>

</template>

<script>
  import utils from '../utils.js'
  import { format } from 'date-fns'
  import { v4 as uuidv4 } from 'uuid'

  export default {
    name: 'TODOHome',
    mixins: [utils],
    data: () => ({
      taskVal: "",
      item: null,
      }),

      methods: {
        addToList() {
          const taskValue = this.taskVal.trim()
          const currDate = new Date()
          const formatDate = format(currDate, "dd MMM HH:mm")
          if(taskValue) {
            this.$store.dispatch(
              "addTodoItem",
            {
              id: uuidv4(),
              name: this.nameVal,
              task: taskValue,
              date: formatDate,
              completed: false,
              editing: false
            })
            this.taskVal = ""
          }
            
        },
      },
      beforeMount() {
      },
    }
</script>

<style>

  .completed {
    opacity: .75;
  }
  .lineThrough {
    text-decoration: line-through;
  }
  .userColor {
    color: hsl(188, 100%, 39%);
  }
  .max-min-width-list {
    max-width: 900px;
    width: 100%;
    min-width: 330px;
  }
  
</style>